.hero {
    padding-top: 100px;
    padding-right: 100px;
    padding-left: 100px;
    height: calc(100vh - 100px);
}

#typing-text {
    position: relative;
    display: inline-block;
}

#typing-text::after {
    content: '';
    position: absolute;
    right: -7vw;
    height: 100%;
    aspect-ratio: 0.544;
    background-color: white;
    animation: blink-caret 0.6s step-end infinite;
}

@keyframes blink-caret {
    from,
    to {
        opacity: 0;
    }
    50% {
        opacity: 1;
    }
}

#typing-text.finished::after {
    display: none;
}

@keyframes typing {
    from {
        width: 0;
    }
    to {
        width: auto;
    }
}

.hero .title {
    white-space: pre-wrap;
    margin-left: -0.4vw;
    position: relative;
    display: flex;
    align-items: end;
}
